.plate {
   background-color: rgb(66, 66, 66);
   border-radius: 25px;
   height: max-content;
   display: flex;
   flex-direction: column;
   /* justify-content: space-evenly; */
   align-items: center;
   /* align-content: space-around; */
   cursor: pointer;
   box-shadow: -10px 10px rgba(0, 0, 0, 0.808);
   transition: 0.5s;
}

.plate:hover {
   box-shadow: 0px 0px rgba(0, 0, 0, 0.658);
   font-size: 90%;
   content: normal;
}
