img {
   height: 300px;
   background-color: rgba(167, 167, 167, 0.651);
   border-radius: 150px;
   transform: scaleX(-1);
}

.circle-container {
   display: flex;
   margin-bottom: 50px;
   align-items: center;
   justify-content: space-evenly;
}

.circle {
   background-color: rgba(167, 167, 167, 0.651);
   width: 100px;
   height: 100px;
   border-radius: 100px;
   display: flex;
   justify-content: center;
   align-items: center;
   cursor: pointer;
   box-shadow: -10px 10px rgba(0, 0, 0, 0.808);
   transition: 0.5s;
}

.circle:hover {
   box-shadow: 0px 0px rgba(0, 0, 0, 0.658);
}
